import React, { useState, useEffect, useCallback } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';

import Nav from './components/Nav/Nav';
import Hero from './components/Hero/Hero';
import AuthModal from './components/Auth/AuthModal';
import Notification from './components/Notification/Notification';
import Todos from './components/Todos/Todos';
import GoodThings from './components/GoodThings/GoodThings';
import Contact from './components/Contact/Contact';
import Footer from './components/Footer/Footer';

import './styles/global.css';

const App = () => {
  const [showAuthModal, setShowAuthModal] = useState(false); // Estado para controlar a exibio da modal de autenticao
  const [isAuthenticated, setIsAuthenticated] = useState(false); // Estado para controlar se o usurio est autenticado
  const [notification, setNotification] = useState({ type: '', message: '' }); // Estado para exibir notificaes

  // Funo para exibir uma notificao com um tempo determinado
  const showNotification = useCallback((type, message) => {
    const newNotification = { id: Date.now(), type, message };
    setNotification(newNotification);
    setTimeout(() => {
      setNotification({ type: '', message: '' });
    }, 3000);
  }, []);

  // Efeito para verificar o estado de autenticao ao carregar o aplicativo
  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      setIsAuthenticated(true);
    }

    // Verifica se h uma notificao armazenada no localStorage e exibe-a
    const storedNotification = localStorage.getItem('notification');
    if (storedNotification) {
      const { type, message } = JSON.parse(storedNotification);
      showNotification(type, message);
      localStorage.removeItem('notification');
    }
  }, [showNotification]);

  // Funo para fechar a modal de autenticao
  const handleCloseAuthModal = () => {
    setShowAuthModal(false);
  };

  // Funo para abrir a modal de autenticao
  const handleLoginClick = () => {
    setShowAuthModal(true);
  };

  // Funo para fazer logout do usurio
  const handleLogoutClick = () => {
    localStorage.removeItem('token');
    setIsAuthenticated(false);
    window.location.reload(); // Recarrega a pgina para refletir o estado de logout
  };

  // Funo chamada aps um login bem-sucedido
  const handleLoginSuccess = () => {
    setIsAuthenticated(true);
    handleCloseAuthModal();
    setTimeout(() => {
      window.location.reload(); // Recarrega a pgina para refletir o estado de login
    }, 1000);
    showNotification('success', 'Successfully logged in'); // Exibe uma notificao de sucesso ao logar
  };

  // Funo para lidar com erros de autenticao
  const handleAuthError = (message) => {
    showNotification('error', message); // Exibe uma notificao de erro com a mensagem fornecida
  };

  // Funo chamada aps um registro bem-sucedido
  const handleRegisterSuccess = () => {
    showNotification('success', 'Successfully registered'); // Exibe uma notificao de sucesso ao registrar
  };

  return (
    <Router>
      <div id="app">
        {/* Barra de navegao */}
        <Nav isAuthenticated={isAuthenticated} onLoginClick={handleLoginClick} onLogoutClick={handleLogoutClick} />
        
        {/* Modal de autenticao */}
        <AuthModal show={showAuthModal} handleClose={handleCloseAuthModal} onLoginSuccess={handleLoginSuccess} onAuthError={handleAuthError} onRegisterSuccess={handleRegisterSuccess} />
        
        {/* Componente de destaque (Hero) */}
        <Hero />
        
        {/* Componente de Tarefas (Todos) */}
        <Todos onLoginClick={handleLoginClick} showNotification={showNotification} />
        
        {/* Componente de Coisas Boas (GoodThings) */}
        <GoodThings />
        
        {/* Componente de Contato (Contact) */}
        <Contact showNotification={showNotification} />
        
        {/* Componente de Notificao */}
        <Notification message={notification.message} type={notification.type} />
      </div>
      
      {/* Rodap */}
      <Footer />
    </Router>
  );
};

export default App;
