import axios from 'axios';

const backendUrl = 'https://enigmatic-harbor-98544-3afc102bbefd.herokuapp.com/';

// Funo para registrar o usurio
const registerUser = async (username, password) => {
  try {
    await axios.post(`${backendUrl}api/auth/register`, { username , password });
  } catch (error) {
    console.log(error);
    throw error; // Lana o erro para ser tratado pelo chamador da funo
  }
};

// Funo para fazer login do usurio
const loginUser = async (username, password) => {
  try {
    const response = await axios.post(`${backendUrl}api/auth/login`, { username, password });
    localStorage.setItem('token', response.data.token); // Armazena o token de autenticao no localStorage
  } catch (error) {
    throw error; // Lana o erro para ser tratado pelo chamador da funo
  }
};

export { registerUser, loginUser };
